@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Mono:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i&display=swap);
@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Sans:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i&display=swap);
///////////////////////  RESET 
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}




body{
    background-color: #292929 !important;
    padding: 0px;
    margin: 0px;
}


.App{
    padding: 96px 0px 56px 0px;
    color: #ffffff;
    height: 1458px;
    max-width: 1440px;
    margin: 0 auto;
}



::-webkit-scrollbar {
    width: 5px;
  }
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 0px transparent;
  }
::-webkit-scrollbar-thumb {
  background-color: #ffffff;
} 


/*For mozilla */
:root{
    scrollbar-color: rgb(255, 255, 255) transparent !important;
    scrollbar-width: thin !important;
  }
*{
    font-family: 'IBM Plex Mono' !important;
}

.nav__content {
    display: grid;
    grid-template-columns: minmax(-webkit-min-content, 576px) minmax(-webkit-min-content, 656px);
    grid-template-columns: minmax(min-content, 576px) minmax(min-content, 656px);
    justify-content: space-between;	
    margin: 0 auto;
    
}

@media screen and (max-width: 834px){
    .nav__content{
        grid-template-columns: minmax(-webkit-min-content, 738px);
        grid-template-columns: minmax(min-content, 738px);
        grid-template-rows: 400px auto;
        grid-gap: 48px 0px;
        padding: 48px 48px;
    }
}

@media screen and (max-width: 564px){
    .nav__content{
        grid-gap: 160px 0px;

    }
}

@media screen and (max-width: 564px){
    .nav__content{
        grid-gap: 180px 0px;
    }

}
h1, h2, h3, h4{
  -webkit-margin-after: unset;
          margin-block-end: unset;
  -webkit-margin-before: unset;
          margin-block-start: unset;
}

.grid__container{
    display: grid;
    grid-gap: 16px 0px ;
    overflow-y: auto;
    overflow-x: hidden;
    height: 728px;
    scrollbar-color: rgb(210,210,210) rgb(46,54,69) !important;
    scrollbar-width: thin !important;
}

.grid__container > a:nth-child(n){
  background: #5E5E5E;
  text-align: center;
  text-decoration: none;
  padding: 32px 0px 0px 48px;
  text-align: left;
  height: 200px;

}

#root > div > div > div:nth-child(2) > section.grid__container > a:nth-child(n) > h3{
  color: #ffffff;
  font-family: IBM Plex Sans !important;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px !important;
  text-transform: uppercase;
}

#root > div > div > div:nth-child(2) > section.grid__container > a:nth-child(n) > h2{
  color: #ffffff;
  font-family: IBM Plex Mono;
  font-style: normal;
  font-weight: bold;
  font-size: 32px;
  line-height: 32px;
  padding: 24px 0px 8px 0px;
}


#root > div > div > div:nth-child(2) > section.grid__container > a:nth-child(n) > h4{
  color: #ffffff;
  font-family: IBM Plex Sans !important;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px !important;
  padding: 0px 0px 32px 0px;
}

#root > div > div > div:nth-child(2) > section.grid__container > a:nth-child(n) > span{
  color: #ffffff;
  font-family: IBM Plex Sans !important;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px !important;
}

#root > div > div > div:nth-child(2){
}

#root > div > div > div:nth-child(2) > section:nth-child(2){
  display: grid;
  grid-gap: 32px 0px;
  position: relative;
  top: 144px;
}




.left__content{
    display: grid;
    height: auto;
    grid-gap: 38px 0px;
}

h1{
    font-size: 80px;
    font-weight: bold;
    line-height: 104px;
    text-align: left;
    vertical-align: top;
    margin-top: 0px;
}

p{
    color: #B3B3B3;
    font-size: 16px;
    font-style: regular;
    line-height: 24px;
    text-align: left;
    vertical-align: top;
    -webkit-margin-before: 0px;
            margin-block-start: 0px;
    -webkit-margin-after: 0px;
            margin-block-end: 0px;
}

.social{
    display: flex;
    align-items: center;
    margin-top: 610px;
}

#root > div > div > div.left__content > section{
    display: grid;
    grid-gap: 0px 0px;
    margin-top: 176px;
}

#proj_contact > a:nth-child(n){
    color: #B2B2B2;
    display: block !important;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    word-spacing: 52px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    text-transform: uppercase;
    text-decoration: none;
}


#root > div > div > div.left__content > section > section.social > img{
    width: 48px;
    height: 48px;
    background: grey;
    border-radius: 25px;
}

#root > div > div > div.left__content > section  > section.social > a:nth-child(n){
    margin-left: 32px;
    color: #FFFFFF;
    text-transform: uppercase;
    text-decoration: none;
}


/* burger menu */

#root > div > div > div.left__content > nav{
    display: none !important;
    right: 0px;
    position: absolute;
    top: 56px;
}

#myLinks {
    display: none;
    top: -22px;
    right: 45px;
    position: relative;
}

#myLinks > a:nth-child(n){
    color: #B2B2B2;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    word-spacing: 52px;
    text-decoration: none;
    text-transform: uppercase;
    padding-right: 54px;
}

#root > div > div > div.left__content > nav > div{
    border-top: 2px solid white;
    border-bottom: 2px solid white;
    width: 20px;
    height: 4px;
}







@media screen and (max-width: 834px){
    .left__content{
        display: grid;
        grid-template-columns: minmax(-webkit-min-content, 576px);
        grid-template-columns: minmax(min-content, 576px);
    }
    
    #root > div > div > div.left__content > section > nav{
        top: 18px;
        left: 12px;
        position: fixed;
    }


    #root > div > div > div.left__content > section > nav > section{
        display: none;
    }

    #root > div > div > div.left__content > section > section{
        display: none;
    }

    /* HAMBURGER */

    #root > div > div > div.left__content > nav{
        display: inline !important;
        display: initial !important;
    }

    #myLinks > a:nth-child(n){
        float: left;
        color: white;
        padding: 14px 16px;
        text-decoration: none;
        font-size: 14px;
        word-spacing: 16px;
    }
    
    #root > div > div > div.left__content > nav > div{
        cursor: pointer;
    }

    #root > div > div > div.left__content > nav > div{
        position: absolute; 
        right: 10px;

    }

}








@media screen and (max-width: 785px){
    #root > div > div > div.left__content > h1{
        font-size: 60px;
        line-height: 84px;

    }
}







@media screen and (max-width: 688px){
    #root > div > div > div.left__content > h1{
        font-family: IBM Plex Mono;
        font-style: normal;
        font-weight: bold;
        font-size: 48px;
        line-height: 62px;
    }

}
