.left__content{
    display: grid;
    height: auto;
    grid-gap: 38px 0px;
}

h1{
    font-size: 80px;
    font-weight: bold;
    line-height: 104px;
    text-align: left;
    vertical-align: top;
    margin-top: 0px;
}

p{
    color: #B3B3B3;
    font-size: 16px;
    font-style: regular;
    line-height: 24px;
    text-align: left;
    vertical-align: top;
    margin-block-start: 0px;
    margin-block-end: 0px;
}

.social{
    display: flex;
    align-items: center;
    margin-top: 610px;
}

#root > div > div > div.left__content > section{
    display: grid;
    grid-gap: 0px 0px;
    margin-top: 176px;
}

#proj_contact > a:nth-child(n){
    color: #B2B2B2;
    display: block !important;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    word-spacing: 52px;
    width: fit-content;
    text-transform: uppercase;
    text-decoration: none;
}


#root > div > div > div.left__content > section > section.social > img{
    width: 48px;
    height: 48px;
    background: grey;
    border-radius: 25px;
}

#root > div > div > div.left__content > section  > section.social > a:nth-child(n){
    margin-left: 32px;
    color: #FFFFFF;
    text-transform: uppercase;
    text-decoration: none;
}


/* burger menu */

#root > div > div > div.left__content > nav{
    display: none !important;
    right: 0px;
    position: absolute;
    top: 56px;
}

#myLinks {
    display: none;
    top: -22px;
    right: 45px;
    position: relative;
}

#myLinks > a:nth-child(n){
    color: #B2B2B2;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    word-spacing: 52px;
    text-decoration: none;
    text-transform: uppercase;
    padding-right: 54px;
}

#root > div > div > div.left__content > nav > div{
    border-top: 2px solid white;
    border-bottom: 2px solid white;
    width: 20px;
    height: 4px;
}







@media screen and (max-width: 834px){
    .left__content{
        display: grid;
        grid-template-columns: minmax(min-content, 576px);
    }
    
    #root > div > div > div.left__content > section > nav{
        top: 18px;
        left: 12px;
        position: fixed;
    }


    #root > div > div > div.left__content > section > nav > section{
        display: none;
    }

    #root > div > div > div.left__content > section > section{
        display: none;
    }

    /* HAMBURGER */

    #root > div > div > div.left__content > nav{
        display: initial !important;
    }

    #myLinks > a:nth-child(n){
        float: left;
        color: white;
        padding: 14px 16px;
        text-decoration: none;
        font-size: 14px;
        word-spacing: 16px;
    }
    
    #root > div > div > div.left__content > nav > div{
        cursor: pointer;
    }

    #root > div > div > div.left__content > nav > div{
        position: absolute; 
        right: 10px;

    }

}








@media screen and (max-width: 785px){
    #root > div > div > div.left__content > h1{
        font-size: 60px;
        line-height: 84px;

    }
}







@media screen and (max-width: 688px){
    #root > div > div > div.left__content > h1{
        font-family: IBM Plex Mono;
        font-style: normal;
        font-weight: bold;
        font-size: 48px;
        line-height: 62px;
    }

}